<template>
	<div>
		<div class="emb-card pa-4 search-box-wrap">
			<div class="d-flex justify-start align-center">
				<div class="dash-title-wrap">
					<h5 class="dash-title mb-0">Departments</h5>
				</div>
				<div class="search-input d-flex justify-space-between align-center">
					<div class="input-wrap">
					</div>
					<div class="action-btn-wrap">
						<v-btn color="primary"  to="/admin-panel/departments-add">
							Add Department
							<v-icon>add</v-icon>
						</v-btn>
					</div>
				</div>
			</div>
		</div>
		<v-layout row wrap class="pt-12 ma-0">
		</v-layout>	
	
		<department-list :data="listData" :gridListView="gridView" :cols="6" :colxl="3" :collg="3" :colmd="4" :colsm="4" :colxs="12">
		</department-list>
	</div>
</template>

<script>
import departmentList from "./departmentList.vue";
import Departments from "Api/department";
export default {
  data() {
    return {
      typeItems: ["Men", "Women", "Gadgets"],
      recentItems: ["This Week", "This Month", "Past Month"],
      noOfItems: ["10", "20", "30"],
      gridView: true,
      listView: false,
      listData: []
    };
  },
  components: {
    departmentList
  },
  methods: {
    switchToGridView(value) {
      this.gridView = value;
    },
    switchToListView(value) {
      this.gridView = value;
    }
  },
  async mounted () {
    try {
      const res = await Departments.getDepartment()
      // console.log()
      res.data.data.forEach(el => {
        // console.log(el)
        const list = {
          en:el.name.en,
           fr:el.name.fr,
            sw:el.name.sw, 
            kiny:el.name.kiny,
            _id:el._id,
            offersServices: el.offersServices,
            offersRental: el.offersRental
            }
        this.listData.push(list)
      });
    } catch (err) {
      console.log(err.message)
    }
  }
};
</script>

